import React, { PureComponent } from 'react'
import Container from 'components/common/container'
import { connect } from 'react-redux'
import { fetchBand } from 'actions/band'
import styled from 'styled-components'
import LazyImage from 'components/common/lazyImage'
import Button from 'components/common/button'
import { mainColor, mainColorDark } from 'components/variables/colors'

const BandWall = styled.div`
    display: block;
    margin-top: -80px;
    padding: 80px 0px;
    background: ${ ( props ) => ( props.image ) ? `url(${ props.image }) #FAFAFA` : '#FAFAFA' };
`

const BandContainer = styled(Container)`
    display: flex;
`
const BandThumb = styled.div`
    display: block;
    width: 200px;
    height: 200px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .7);
`

const BandDetails = styled.div`
    display: block;
    width: auto;
    padding: 6px 12px;
`

const BandTitle = styled.h1`
    display: block;
    color: #fff;
    font-size: 3rem;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, .7);
`

const BandButton = styled(Button)`
    border-radius: 50%;
    margin-right: 12px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .7);
    height: 42px;
    min-width: 42px;
    line-height: 42px;
    font-size: 2rem;
    padding: 0px;
`

@connect( ( store ) => {
    return {
        search: store.search,
        band: store.band,
    }
} )

export default class BandPage extends PureComponent {

    constructor ( props ) {
        super( props )

        this.state = {
            band: null,
        }

    }

    static getDerivedStateFromProps( nextProps, prevState ) {

        const { band } = nextProps.match.params
        const prevBand = ( prevState ) ? prevState.band : null

        if ( band !== prevBand ) {
            nextProps.dispatch( fetchBand( band ) )
        }

        return {
            band: band
        }

    }

    render () {

        const { band } = this.props

        if ( !band.data ) {
            return null
        } else {

            const { image_url, thumb_url, name, facebook_page_url } = band.data

            return (
                <div>
                    <BandWall image={ image_url }>
                        <BandContainer>
                            <BandThumb>
                                <LazyImage width={ 200 } height={ 200 } image={ thumb_url } title={ name } />
                            </BandThumb>
                            <BandDetails>
                                <BandTitle>{ name }</BandTitle>
                                { ( facebook_page_url ) ? (
                                    <a href={ facebook_page_url } target='_blank'>
                                        <BandButton background='#ffffff' color={ mainColor }>
                                            <i className='icon icon-facebook' />
                                        </BandButton>
                                    </a>
                                ) : null }
                                <BandButton background='#ffffff' color={ mainColor }>
                                    <i className='icon icon-heart' />
                                </BandButton>
                            </BandDetails>
                        </BandContainer>
                    </BandWall>
                    <Container>
                        <pre>{ JSON.stringify( band ) }</pre>
                    </Container>
                </div>
            )
        }
    }

}
